"use client";
import Image from "next/image";
import { useEffect, useState } from "react";

export default function Home() {
  const [scale, setScale] = useState(1);

  useEffect(() => {
    const updateScale = () => {
      const baseWidth = 1280;
      const baseHeight = 720;
      const scaleX = window.innerWidth / baseWidth;
      const scaleY = window.innerHeight / baseHeight;
      setScale(Math.min(scaleX, scaleY));
    };

    updateScale();
    window.addEventListener("resize", updateScale);
    return () => window.removeEventListener("resize", updateScale);
  }, []);

  return (
    <main
      style={{
        width: "100vw",
        height: "100vh",
        backgroundColor: "black",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        overflow: "hidden",
      }}
    >
      <div
        style={{
          width: 1280,
          height: 720,
          transform: `scale(${scale})`,
          transformOrigin: "top left",
          position: "relative",
          boxShadow: "0 0 24px rgba(0,0,0,0.3)",
          background: "#fff",
        }}
      >
        {/* 背景 */}
        <Image
          src="/game_00.png"
          alt="遊戲背景"
          fill
          style={{
            objectFit: "cover",
            zIndex: 0,
          }}
          priority
        />

        {/* 公告欄 */}
        <div
          style={{
            position: "absolute",
            top: 500,
            left: 340,
            width: 600,
            height: 180,
            zIndex: 1,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Image
            src="/game_01.png"
            alt="公告欄底圖"
            width={600}
            height={180}
            style={{
              position: "absolute",
              left: 0,
              top: 0,
              zIndex: 1,
              pointerEvents: "none",
            }}
          />
          <div
            style={{
              position: "relative",
              width: 520,
              color: "#222",
              fontWeight: "bold",
              fontSize: 14,
              lineHeight: "22px",
              background: "rgba(255,255,255,0.7)",
              borderRadius: 8,
              padding: "12px 24px",
              zIndex: 2,
              margin: "0 auto",
            }}
          >
            <ul style={{ listStyle: "none", padding: 0, margin: 0 }}>
              <li>● 每個月將舉行一次主題日活動，有小比賽進行，獎勵等你拿!</li>
              <li>● 每周遊戲將由參加者自行決定要玩的桌遊品項，共四款</li>
              <li>● 每個月有一周為本社團休息周，不另做公告</li>
              <li>● 遊戲研究社日程規劃，詳細請看遊戲研究社日程表</li>
              <li>● 歡迎推薦遊戲，將視社費狀況進行購買</li>
            </ul>
          </div>
        </div>

        {/* 小島 */}
        <Image
          src="/game_02.png"
          alt="右上角小島"              
          width={220}
          height={180}
          style={{
            position: "absolute",
            top: 150,
            left: 950,
            zIndex: 2,
            pointerEvents: "none",
          }}
        />
      </div>
    </main>
  );
}

